<template>
  <el-container>
    <el-header style="font-size: 25px; padding: 5px">
      <h5 class="card-title" style="font-size: 30px; padding: 5px">
        车辆历史轨迹查询
      </h5>
    </el-header>
    <el-main>
      <div class="card-header"></div>
      <div class="card-body">
        <div class="card-body" style="padding: 5px">
          <!-- <dv-active-ring-chart :config="config_jgzm" style="width:25vw;height: 20vh;" /> -->
        </div>

        <h5 class="card-title" style="font-size: 25px; padding: 5px">
          车辆选择
        </h5>
        <el-date-picker
          v-model="value"
          type="datetimerange"
          unlink-panels
          range-separator="到"
          start-placeholder="选择开始时间"
          end-placeholder="选择结束时间"
          :shortcuts="shortcuts"
          :disabled-date="disabledDate"
          @change="changeDate"
          size="large"
        />
        <!-- <el-button @click="excuteQuery" size="large" type="primary" style="padding:20px">查询</el-button> -->
        <el-select
          v-model="carValue"
          placeholder="选择车牌信息"
          style="font-size: 25px; padding: 5px"
          size="large"
        >
                  <el-option-group label="天府环境" value="天府环境">
            <el-option
              v-for="car in carListTianfu"
              :key="car.carNumber"
              :label="car.carNumber"
              :value="car.carNumber"
            >
              <span style="float: left">{{ car.carNumber }}</span>
              <span
                style="
                  float: right;
                  color: var(--el-text-color-secondary);
                  font-size: 13px;
                  padding: 5px;
                "
                >{{ car.company }}</span
              >
            </el-option>
          </el-option-group>
          <el-option-group label="仁和星牛" value="仁和星牛">
            <el-option
              v-for="car in carListRenhe"
              :key="car.carNumber"
              :label="car.carNumber"
              :value="car.carNumber"
            >
              <span style="float: left">{{ car.carNumber }}</span>
              <span
                style="
                  float: right;
                  color: var(--el-text-color-secondary);
                  font-size: 13px;
                  
                "
                >{{ car.company }}</span
              >
            </el-option>
          </el-option-group>

        </el-select>
        <el-button @click="showCars()">点击搜索</el-button>
      </div>

      <div class="mapDiv">
        <tdt-map :center="state.center" :zoom="state.zoom" mapStyle="black">
          <tdt-polygon
            :path="polygon.path"
            :opacity="0.8"
            :visible="polygon.visible"
            :lineStyle="polygon.lineStyle"
            :fillColor="polygon.fillColor"
          ></tdt-polygon>
          <tdt-marker
            :position="current_car.location"
            :icon="current_car.icon"
          ></tdt-marker>
          <tdt-polyline
            v-model:path="current_car.current_path"
            color="#FF0000"
            :opacity="0.8"
            :visible="current_car.carVisible"
          ></tdt-polyline>
        </tdt-map>
      </div>
    </el-main>
