  getCars().then((data) => {
    carData.value = data;

    for (var i = 0; i < carData.value.length; i++) {

            if (carData.value[i].company == "天府环境") {
        var car = {
          carNumber: carData.value[i].carNumber,
          company: "天府环境",
        };
        carListTianfu.value.push(car);
        //console.log(carListTianfu.value)
      }
      if (carData.value[i].company == "仁和星牛") {
