        carListRenhe.value.push(car);
      }


    }
  });
});
onMounted(() => {
  if (route.query.carNumber) {
    carValue.value = route.query.carNumber;

    showCars();
  }
});
function showCars() {
  console.log("车辆：" + carValue.value);

  getCars().then((data) => {
    for (var i = 0; i < data.length; i++) {
      if (data[i].carNumber == carValue.value) {
        current_car.company = data[i].company;
        console.log("公司"+data[i].company)
        if (data[i].company == "天府环境") {
          current_car.icon = current_car_tianfu.icon;
        }
        if (data[i].company == "仁和星牛") {
          current_car.icon = current_car_renhe.icon;
        }
      }
    }
  });
  getCarGps(carValue.value, start, end).then((data) => {
    console.log(data);
    if (data == "error: 车牌号不存在或查询操作太频繁") {
      ElMessage({
        message: h("p", null, [h("span", null, "车辆在指定时间内暂无轨迹！")]),
        type: "error",
      });
    } else {
      current_car.location = [
        data[data.length - 1].longitude * 1,
        data[data.length - 1].latitude * 1,
      ];
      var pointList = [];
      for (let i = 0; i < data.length; i++) {
        var point = [data[i].longitude * 1, data[i].latitude * 1];

        pointList.push(point);
      }
      console.log(pointList);
      current_car.current_path = pointList;
      state.center = current_car.location;

    }
  });
}
function changeDate() {
  start =
    value.value[0].getFullYear() +
    "-" +
    (value.value[0].getMonth() + 1) +
    "-" +
    value.value[0].getDate() +
    "T" +
