    ":" +
    value.value[0].getMinutes().toString().padStart(2, 0) +
    ":" +
    value.value[0].getSeconds().toString().padStart(2, 0);

  end =
    value.value[1].getFullYear() +
    "-" +
    (value.value[1].getMonth() + 1) +
    "-" +
    value.value[1].getDate() +
    "T" +
