function getDateList(count, long) {
  var time = new Date().setMinutes(0);
  time = time - 6 * 24 * 60 * 60 * 1000;
  var categoryData = [];
  for (var i = 0; i <= count; i++) {
    categoryData.push(moment(time).format("MM-DD"));
    time += long;
  }
  return categoryData;
}
