// ======================================================================================================sunny
/**
 * 右上图：展示仁和星牛最近4个小时内活动车辆数，以及未活动车辆数
 * 用柱状图
 * 最近4个小时：前端直接通过现有接口，拿到54辆车的最近一次gsp记录后，遍历，挨个判断每个记录的时间戳和当前时间的差，大于四个小时就丢到没活跃的集合里面去。
 */

//定义total
const total1 = ref(2);
//全局定义图表
let renhe_chart = null;

//图表的基础模板
let renheOption = {
  color: ["#41C9CE", "#DC8D75"],
  tooltip: {
    trigger: "item",
  },
  legend: {
    bottom: "2%",
    left: "center",
    selectedMode: false,

    textStyle: {
      color: "rgba(255,255,255,0.8)",
      fontSize: "12",
    },
  },

  series: [
    {
      name: "仁和星牛车辆",
      //设置图表类型是折线图
      type: "pie",
      radius: ["35%", "65%"],
      center: ["50%", "40%"],

      startAngle: 180,
      label: {
        show: false,
        position: "center",
        formatter(param) {
          // correct the percentage
          return param.name + " (" + param.percent + "%)";
        },
        color: "#fff",
      },
      emphasis: {
        label: {
          show: true,
          fontSize: 20,
          fontWeight: "bold",
        },
      },
      itemStyle: {
        borderRadius: 5,
        borderColor: "#fff",
        borderWidth: 2,
      },
      data: [
        { value: 0, name: "在用" },
        { value: 0, name: "停用" },
        {
          // make an record to fill the bottom 50%

          itemStyle: {
            // stop the chart from rendering this piece
            color: "none",
            decal: {
              symbol: "none",
            },
          },
          label: {
            show: false,
          },
        },
      ],
    },
  ],
};

/**
 * 获取后端数据，并动态展示在图表上
 */
const create_renhe_data = () => {
  let chartDom = document.getElementById("renhe_pie");
  //初始化图表
  renhe_chart = echarts.init(chartDom);

  // renhe_chart.on("click", (params) => {
  //   console.log("params:" + params.data.name);
  //   renheVisible.value = true;
  // });

  //绘制图表
  renhe_chart.setOption(renheOption);
  window.addEventListener("resize", renhe_chart.resize);

  getAllGps().then((resp) => {
    let num = 0;
    for (var car in resp) {
      let gps_time = Date.parse(resp[car].exactDate.replace("T", " "));
      resp[car].carNumber = trim(resp[car].carNumber);
      if (renhe_car.has(resp[car].carNumber.toUpperCase())) {
        if (gps_time + 4 * 60 * 60 * 1000 > time) {
          num++;
        }
      }
    }
    renheOption.series[0].data[0].value = num;
    renheOption.series[0].data[1].value = renhe_car.size - num;
    //更新页面上的图表
    renhe_chart.setOption(renheOption);
  });
};

//删除字符串中多余的空格
function trim(ele) {
  if (typeof ele === "string") {
    return ele.split(" ").join("");
  } else {
    console.error(
      `${typeof ele} is not the expected type, but the string type is expected`
    );
  }
}

