/**
 * Dom的挂载和销毁
 */
//Dom挂载完毕，可以拿到组件渲染后的 DOM节点，展示图表
onMounted(() => {
  create_small_data();
  create_tow_data();
  create_category_data();

  create_renhe_data();

});
function handleClick() {
//  create_renhe_data(); 
